.rt-card {
    position: relative;
    border-radius: 10px;
    border: 1px solid #E8E8E8;
    background: $color-white;
    overflow: hidden;
    z-index: 0;
    @include transition;
    
    .item-img {
        position: relative;
        overflow: hidden;
        z-index: 0;
        
        a {
            width: 100%;
            @include transition;
            img {
                width: 100%;
                @include transition;
            }
        }
    }

    .item-action {
        .doctor-category {
            position: absolute;
            left: 20px;
            top: 20px;
            a {
                color: #928DA9;
                font-size: 14px;
                font-weight: 600;
                line-height: 20px;
                padding: 5px 14px;
                border-radius: 50px;
                background-color: $color-white;

                &:hover {
                    background-color: $color-primary;
                    color: $color-white;
                }
            }
        }
        .favourite {
            position: absolute;
            right: 20px;
            top: 20px;
            a {
                width: 36px;
                height: 36px;
                background-color: $color-white;
                color: #928DA9;
                &:hover {
                    background-color: $color-primary;
                    color: $color-white;
                }
            }
        }
    }

    .item-content {
        padding: 24px 24px 19px;
        .title {
            margin-bottom: 10px;
            line-height: normal;
            a {
                color: #41396A;
                &:hover {
                    color: $color-primary;
                }
            }
        }
        .designation {
            font-size: 14px;
            font-weight: 600;
            line-height: 120%;
            margin-bottom: 15px;
        }
        .meta-list {
            margin-bottom: 20px;
            li, a {
                color: $color-text;
                font-size: 16px;
                font-weight: 400;
                line-height: normal;
                margin-bottom: 14px;
                &:last-child {
                    margin-bottom: 0;
                }
                i {
                    margin-right: 5px;
                }
            }
        }
    }

    .card-btn {
        gap: 10px;
        padding: 15px 20px 16px 36px;
        border-radius: 8px;
        font-size: 16px;
        font-weight: 600;
        line-height: normal;
        i {
            
        }
    }

    &:hover {
        .item-img {
            img {
                transform: scale(1.18);
                transform-origin: top;
            }
        }
        .card-btn { 
            i {
                
            }
        }
    }

    &--style-1 {
    }

    &--style-2 {
        border-radius: 10px;
        background: $color-white;
        padding: 10px 30px 10px 10px;
        border-color: transparent;
        .icon {
            background-color: $color-secondary;
            border-radius: 10px;
            width: 120px;
            height: 130px;
            @include transition;
            svg {
                @include transition;
            }
        }
        .item-content {
            padding: 0;
            flex: 1;

            .title {
                margin-bottom: 14px;
                a {
                    color: $color-title;
                }
            }
            .para-text {
                font-size: 15px;
                font-weight: 500;
                color: #928DA9;
                line-height: normal;
            }

            .arrow-icon {
                width: 40px;
                height: 40px;
                border: 1px solid #E4E4E4;
                @include transition;
                i {
                    font-size: 12px;
                }
                &:hover {
                    background-color: $color-primary;
                    color: $color-white;
                    border-color: transparent;
                }
            }
        }

        &:hover {
            box-shadow: 0px 0px 50px 0px rgba(0, 0, 0, 0.10);
            .icon {
                background-color: $color-primary;
                svg {
                    fill: $color-white;
                }
            }
        }
    }

    &--style-3 {
        .item-action {
            .favourite {
                position: absolute;
                right: unset;
                left: 20px;
                top: 20px;
            }
        }

        .item-img {
            a {
                height: 100%;
                img {
                    height: 100%;
                }
            }
        }
        
        .item-content {
            flex: 1;
            padding: 22px 45px 27px 24px;
            @include media-from-to (1200px, min, 1400px, max) {
                padding: 22px 15px 27px 24px;
            }
            .title {
                a {
                    color: $color-title;
                }
            }
            .card-text {
                margin-bottom: 19px;
            }

            .meta-list {
                margin-bottom: 28px;
                li {
                    margin-bottom: 0;
                    color: $color-title;
                    font-size: 15px;
                    font-weight: 600;
                    line-height: normal;
                    text-transform: capitalize;
                    i {
                        color: $color-primary;
                    }
                }
            }

            .card-btn {
                padding: 12px 13px;
            }
        }
    }
    &--style-4 {
        border: none;
        .item-img {
            border-radius: 10px;
            overflow: hidden;
            margin-bottom: 40px;
            img {
                @include transition;
            }
        }
        .item-content {
            padding: 0;
            padding-right: 20px;

            .title {
                font-size: 30px;
                font-weight: 600;
                line-height: 45px;
                margin-bottom: 13px;
                a {
                    color: $color-title;
                }
            }

            .para-text {
                margin-bottom: 30px;
            }

            .meta-list {
                gap: 40px;
                li, a {
                    margin-bottom: 0;
                    font-size: 18px;
                    i {
                        margin-right: 3px;
                    }
                }
            }
            .read-more {
                padding: 13px 24px;
            }
        }
    }
    &--style-5 {
        border: none;
        border-radius: 0;
        gap: 24px;
        padding-bottom: 35px;
        margin-bottom: 35px;
        border-bottom: 1px solid #D9D9D9;;
        .item-img {
            border-radius: 10px;
            overflow: hidden;
            display: inline-block;
            img {
                @include transition;
            }
        }
        .item-content {
            padding: 0;
            flex: 1;

            .title {
                font-weight: 600;
                line-height: 30px;
                margin-bottom: 13px;
                a {
                    color: $color-title;
                }
            }

            .para-text {
                margin-bottom: 0;
            }

            .meta-list {
                gap: 20px;
                @include media-only ('md', min) {
                    gap: 40px;
                }
                li {
                    margin-bottom: 0;
                    // font-size: 16px;
                    i {
                        margin-right: 3px;
                    }
                }
            }
            .read-more {
                padding: 13px 24px;
            }
        }
    }
    &--style-6 {
        margin-left: 49px;
        position: relative;
        border: none;
        overflow: visible;
        
        .item-img {
            width: 98px;
            height: 98px;
            position: absolute;
            top: 24px;
            left: -49px;
            border: 4px solid #C3B8F8;
            background-color: #EDEDED;
        }

        .item-content {
            background-color: #FAFAFA; 
            border-radius: 10px 10px 0 0;
            padding: 24px 23px 20px 69px;
            .meta-list {
                margin-bottom: 0;
            }
        }

        .item-action {
            margin-bottom: 10px;
            .doctor-category {
                position: relative;
                left: unset;
                top: unset;
                a {
                    color: $color-primary;
                    background-color: #ECE8FD;
                    &:hover {
                        background-color: $color-primary;
                        color: $color-white;
                    }
                }
            }
            .favourite {
                position: relative;
                right: unset;
                top: unset;
                a {
                    color: $color-primary;
                    background-color: #ECE8FD;
                    &:hover {
                        background-color: $color-primary;
                        color: $color-white;
                    }
                }
            }
        }

        

        .card-btn {
            border-radius: 0px 0px 6px 6px;
        }
    }

    &--style-7 {
        width: 244px;
        height: 236px;
        flex: 1;
        border-radius: 15px;
        border: 1px solid var(--blue-blue-50, #E7E6EC);
        background: $color-white;
        margin-bottom: -114px;
        z-index: 1;
        .icon {
            width: 80px;
            height: 80px;
            background-color: $color-secondary;
            margin-bottom: 16px;
            @include transition;
        }
        .item-content {
            padding: 0;
            .title-content {
                .title {
                    font-size: 20px;
                    margin-bottom: 4px;
                    a {
                        color: $color-title;
                    }
                }
                .para-text {
                    font-size: 14px;
                    font-weight: 500;
                }
            }
        }

        &:hover {
            .icon {
                background-color: $color-primary;
                svg {
                    fill: $color-white;
                }
            }
        }
    }

    &--style-8 {
        border: none;
        position: relative;
        padding-top: 76px;
        overflow: visible;
        @include transition;
        .item-img {
            display: inline-block;
            position: absolute;
            top: -20px;
            text-align: center;
            left: 0;
            right: 0;
            padding: 20px;
            @include transition;
            img {
                @include transition;
                border: 10px solid $color-white;
            }
        }
        .item-content {
            padding: 100px 20px 34px 20px;
            border-radius: 30px 30px 10px 10px;
            background: #F7F5FF;
            @include transition;
            .title {
                font-weight: 600;
                a {
                    color: $color-title;
                }
            }
            p {
                font-size: 15px;
                color: #41396A;
                @include transition;
            }
        }

        &:hover {
            .item-img { 
                img {
                    transform: unset;
                    box-shadow: (0px 0px 50px rgba(0, 0, 0, 0.10));
                }
            }
            .item-content { 
                background-color:  $color-primary;
                .title {
                    a {
                        color: $color-white;
                    }
                }
                p {
                    color: #E7E6EC;
                }
            }
        }
    }

    &--style-9 {
        border-radius: 8px;
        background: $color-white;
        box-shadow: 0px 5px 0px 0px rgba(0, 0, 0, 0.10);
        padding: 41px 34px 50px 34px;

        .icon {
            margin-bottom: 25px;
        }

        .text {
            font-size: 18px;
            line-height: 28px;
            color: $color-title;
            margin-bottom: 32px;
        }

        .card-bottom {
            .commentor-info {
                .title {
                    font-weight: 600;
                    line-height: normal;
                    margin-bottom: 3px;
                }
                .designation {
                    font-size: 15px;
                }
            }
    
            .avatar {
                width: 60px;
                height: 60px;
            }
        }
    }
}



.doctor-listing-section {
    .row {
        --bs-gutter-y: 24px;
    }
}